.App {
  text-align: center;
  // min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.GameWrapper {
  width: 100%;
}

.BoardWrapper {
  margin: 0 auto;
  box-sizing: border-box;
  position: relative;
  border-radius: 6px;
  // cursor: default;
  background: rgb(187, 173, 160);
}

.GridWrapper {
  position: absolute;
  z-index: 1; 
  display:flex;
  flex-direction: column;
}

.Row {
  display:flex;
  margin-bottom: 10px;
  &.Row:last-child {
    margin-bottom: 0; 
  }
}
  
.Cell {
  width: 100px;
  height: 100px;
  border-radius: 3px;
  background: rgba(238, 228, 218, 0.35);
  .Cell:last-child {
    margin-right: 0; 
  }
}

.TileListWrapper {
  position: absolute;
  z-index: 1; 
}

.Tile {
  position: absolute;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  font-weight: bold;
  transition: transform 300ms ease-in-out; // check
}

.Value {
  &-2 { background: rgb(238, 228, 218); }
  &-4 { background: rgb(237, 224, 200); }
}

.Value-8, .Value-16, .Value-32, .Value-64 {  color: rgb(249, 246, 242); }
.Value {
  &-8 { background: rgb(242, 177, 121); }
  &-16 { background: rgb(245, 149, 99); }
  &-32 { background: rgb(246, 124, 95); }
  &-64 { background: rgb(246, 94, 59); }
}

.Value-128, .Value-256, .Value-512 { color: rgb(249, 246, 242); }
.Value {
  &-128 { background: rgb(237, 207, 114); }
  &-256 { background: rgb(237, 204, 97); }
  &-512 { background: rgb(237, 200, 80); }
}

.Value-1024, .Value-2048 { color: rgb(249, 246, 242); }
.Value {
  &-1024 { background: rgb(237, 197, 63); }
  &-2048 { background: rgb(237, 194, 46); }
}

.Merged {
  z-index: 10;
  animation: merge 220ms ease;
}

@keyframes merge {
  0% {
    transform: scale(0.5); }

  50% {
    transform: scale(1.1); }

  100% {
    transform: scale(1); } 
}

.Controls {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ButtonWrapper {
  flex: 1;
}

.Button {
  background-color: rgb(250, 248, 240);
  color:none;
  border: none;
  outline:none;
}

.IconWrapper {
  margin: 0.5rem;
  font-size: 2rem;
  cursor: pointer;
  color: #776e65;
  &:active {
    transition: 50ms;
    transform: scale(0.9);
  }
}

.Description {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-size: 1rem;
  p {
    margin: 6px 30px;
    text-align: start;
  }
}

.Settings {
  position: fixed;
  z-index: 50;
  top: 25%;
  left: calc(50% - 206px);
  display: none;
  height: 180px;
  width: 400px;
  border: 6px solid #776e65;
  border-radius: 6px;
  background-color: rgb(250, 248, 240);
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.OpenModal {
  display: flex;
}
